import { Component, ViewChild } from '@angular/core';
import { IonicPage, MenuController, NavController, Platform, ToastController, LoadingController, AlertController, ModalController } from 'ionic-angular';

import { Slides } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { RegisterPage } from '../register/register';
import { ValidatorProvider } from '../../providers/validator/validator'
import { ConnectivityServiceProvider } from '../../providers/connectivity-service/connectivity-service';

@IonicPage()
@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html',
})
export class TutorialPage {


  dir: string = "ltr";
  @ViewChild( "slider" ) slider: Slides;
  slideIndex = 0;
  slides = [];
  error: number = 0;
  objectblock: any = { type: 'slides_', lang: 'en' };
  isPhoneSaved = false;
  relationship: any = "en"
  myValue: any;
  phoneError: boolean = false;
  encryptIsPresent = false;
  isCustomer = false;
  langs: any = {};
  language: any = 'ki';
  light: any = 'pbzyellow';
  pager: boolean = true;
  auto: any = '3000';
  isdisable = true;
  constructor( public platform: Platform, public validator: ValidatorProvider, private storage: Storage, private clientdata: ClientdataProvider, public navCtrl: NavController
    , public modalCtrl: ModalController, public mymenu: MenuController, public menu: MenuController,
    private connect: ConnectivityServiceProvider, public globalVars: GlobalVarsProvider, public loadingController: LoadingController,
    public alertService: AlertServiceProvider, public alertCtrl: AlertController ) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.language = this.globalVars.getLang();
    this.dir = platform.dir();
    this.slides = [
      {
        title: this.langs.in_charge,
        description: this.langs.incharge_loan,
        imageUrl: 'assets/slide2.png',
      },
      {
        title: this.langs.easy,
        description: this.langs.created_mind,
        imageUrl: 'assets/slide1.png',
      } ];

    if ( this.connect.isOnline ) {
      this.isdisable = false;
      this.submit();
    } else { 
      this.popmeth( this.langs.internet );
    }
  }

  popmeth( message ) {
    let template = "<div>" + message + "</div>";
    let obj = { body: "", template: template, ttype: "", completed: true, pageTo: '' };
    let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
    myModal.present();
    myModal.onDidDismiss( data => {
      if ( data ) {
        this.alertService.directExit();
      }
      else {
        this.alertService.directExit();
      }
    } );
  }
  onPageDidEnter() {
    this.globalVars.pause();
    this.menu.enable( false );
  }
  changelang( langus ) {
    console.log( langus );
    if ( langus == 'ki' ) {
      this.globalVars.setLang( 'ki' );
      this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    }
    else if ( langus == 'en' ) {
      this.globalVars.setLang( 'en' );
      this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    }
  }
  goRegister() {
    this.navCtrl.setRoot( 'LoginPage' );
  }
  userNameChanged() {

    if ( this.objectblock.username.length >= 10 ) {
      this.setNumber();
    }
  }
  setNumber() {
    if ( this.objectblock.username === "" || this.objectblock.username === undefined || this.objectblock.username.length < 9 || !this.globalVars.trimPhome( this.objectblock.username ).startsWith( '2557' ) ) {
      this.error = 0;
    } else {
      this.globalVars.setUsername( "255" + this.objectblock.username.substr( this.objectblock.username.length - 9 ) );
      this.checkExistence();
    }
  }
  checkExistence() {
    this.encryptIsPresent = true;
    this.navCtrl.push( 'PinModalPage' );
    this.isCustomer = true;
    this.globalVars.setUsername( "255" + this.objectblock.username.substr( this.objectblock.username.length - 9 ) );
   
  }
  backD() {
    this.alertService.showPin();
  }
  onSlideChanged() {
    this.slideIndex = this.slider.getActiveIndex();
  }
  goToApp() {
    this.navCtrl.setRoot( 'LoginPage' );
  }
  skip() {
    this.slider.slideTo( 4, 500 );
    this.light = "primary";
    this.auto = '30000';
    this.pager = false;
  }
  nextSlide() {
    this.slider.slideNext();
  }

  submit() {
    console.log( this.objectblock);
      this.clientdata.faq( this.objectblock )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if ( data.length != 0 ) {
            if ( !data.error ) {
              this.slides = [ ...this.slides, ...data.data ];
              this.slides.splice( 0, 1 );
              this.slider.initialSlide
            } else if ( data.error ) {
              this.alertService.mytoastdown( this.langs.tech_error);
            } else {
              this.alertService.mytoastdown( this.langs.tech_error );
            }
          } else {
            this.alertService.mytoastdown( this.langs.tech_error );
          }
        }, err => {
          this.alertService.mytoastdown( this.langs.internet );
        } );
    }
  }  







